.root {
  --fontFamily: "Open Sans", sans-serif;
  --fontWeight: 600;
  --fontSize: 18px;
  --lineHeight: 30px;
  --letterSpacing: .02em;
  --white: #fff;
  --orange: #ff6c00;
  --darkGrey: #303030;
  --lightGrey: #9da4bd;
}

body {
  font-family: var(--fontFamily);
  font-weight: var(--fontWeight);
  font-size: var(--fontSize);
  line-height: var(--lineHeight);
  letter-spacing: var(--letterSpacing);
  color: var(--darkGrey);
  margin: 0;
}

.container {
  margin: 0 auto;
}

.first-section {
  flex-direction: row;
  margin-left: 160px;
  display: flex;
}

.first {
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("slider.a8158aa3.html");
  background-repeat: no-repeat;
  background-size: cover;
  width: 1070px;
}

.header {
  flex-direction: row;
  display: flex;
  position: relative;
}

.header__nav {
  flex-direction: row;
  gap: 30px;
  margin: 52px auto 0 100px;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__contact {
  flex-direction: row;
  align-items: center;
  gap: 40px;
  margin: 52px 20px 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item, .header__contact-item {
  text-decoration: none;
}

.header__nav-item {
  letter-spacing: .1em;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
}

.header__nav-item:hover, .header__nav-item:focus, .header__contact-item:hover, .header :focus {
  color: #ff6c00;
  cursor: pointer;
}

.header__nav-list {
  flex-flow: row;
  justify-content: flex-end;
  gap: 64px;
  margin: 100px 20px 40px 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-link {
  text-align: right;
  letter-spacing: .1em;
  color: #9da4bd;
  padding: 0;
  font-size: 11px;
  font-weight: 600;
  line-height: 15px;
  text-decoration: none;
}

.header__nav-link:hover, .header__nav-link:focus {
  color: #fff;
}

.header__contact-item, .header__btn {
  letter-spacing: .1em;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
}

.header__btn {
  background: none;
  border: 1px solid #ff6c00;
  border-radius: 25px;
  padding: 12px 20px;
}

.header__btn:hover, .header__btn:focus {
  cursor: pointer;
}

.main {
  flex-direction: column;
  gap: 20px;
  margin: 182px auto 0 100px;
  display: flex;
  position: relative;
}

.main__icon {
  fill: #9da4bd;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 6px;
}

.main__description {
  text-transform: uppercase;
  color: rgba(157, 164, 189, .6);
  margin: 0 0 0 80px;
  padding: 0;
  font-size: 11px;
  font-weight: 600;
  line-height: 15px;
}

.main__description:before {
  content: " ";
  background: currentColor;
  width: 60px;
  height: 1px;
  margin: 5px auto -6px -80px;
  display: block;
}

.main__title {
  letter-spacing: .05em;
  color: #fff;
  margin: 0;
  padding: 0;
  font-size: 72px;
  font-weight: 700;
  line-height: 85px;
}

.main__text {
  letter-spacing: .02em;
  color: #9da4bd;
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

.sidebar {
  flex-direction: column-reverse;
  height: 680px;
  display: flex;
  position: relative;
}

.sidebar__logo {
  position: absolute;
  top: 32px;
  right: 32px;
}

.sidebar-nav__icon {
  fill: #000;
  margin: 0;
  padding: 0;
  transform: rotate(-90deg);
}

.sidebar-nav__list {
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
  gap: 100px;
  margin: 0 12px 60px 0;
  padding: 0;
  display: flex;
}

.sidebar-nav__list:before {
  content: " ";
  background: gray;
  width: 60px;
  height: 1px;
  display: block;
  transform: rotate(-90deg);
}

.sidebar-nav__link {
  letter-spacing: .2em;
  color: #303030;
  font-size: 11px;
  font-weight: 600;
  line-height: 15px;
  text-decoration: none;
  transform: rotate(-90deg);
}

.sidebar-nav__link:hover, .sidebar-nav__link:focus {
  color: #ff6c00;
  cursor: pointer;
}

.sidebar-nav__link__logo {
  position: absolute;
  top: 32px;
  right: 32px;
}

.slider {
  display: none;
}

@media screen and (min-width: 768px) {
  .slider {
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
  }
}

.slider__pagination {
  display: none;
}

@media screen and (min-width: 768px) {
  .slider__pagination {
    z-index: 9;
    cursor: pointer;
    transition: background-color var(--fast-anim);
    flex-direction: column;
    align-items: flex-end;
    width: 18px;
    display: flex;
    position: absolute;
    top: 50%;
    right: 39px;
  }
}

.slider__item {
  background-color: var(--color-text-light);
  width: 8px;
  height: 2px;
}

.slider__item:not(:last-child) {
  margin-bottom: 12px;
}

.slider__item.active {
  background-color: var(--color-text-white);
  width: 100%;
}

.slider__controls {
  flex-direction: row;
  gap: 30px;
  margin: 52px auto 0 100px;
  padding: 0;
  list-style: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  .slider__controls {
    display: flex;
    position: absolute;
    bottom: 40px;
    right: 39px;
  }
}

.slider__controls-item {
  color: #9da4bd;
  letter-spacing: .1em;
  z-index: 999;
  padding: 0;
  font-size: 11px;
  font-weight: 400;
  line-height: 15px;
  text-decoration: none;
  list-style: none;
}

.slider__controls-item:hover, .slider__controls-item:focus {
  color: #fff;
  cursor: pointer;
}

.slides {
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;
  display: flex;
}

.slide {
  box-sizing: border-box;
  transform-origin: center;
  scroll-snap-align: center;
  background: linear-gradient(to right, #fff 12%, rgba(25, 28, 38, .12));
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 580px;
  margin-right: 0;
  display: flex;
  transform: scale(1);
}

@media screen and (min-width: 768px) {
  .slide {
    max-width: 768px;
    height: 680px;
  }
}

@media screen and (min-width: 1280px) {
  .slide {
    max-width: 1280px;
  }
}

.slide__img {
  width: 100%;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

